.sidebar_container {
  padding: 0 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.menu {
  position: fixed;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.nav {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}

.link {
  text-decoration: none;
  color: #bbb;
  font-weight: 500;
  transition: all 0.3s ease;
  margin-bottom: 20px;
  font-size: 20px;
}

.activeLink,
.link:hover {
  letter-spacing: 2px;
  color: #fff;
}

@media (max-width: 1024px) {
  .sidebar_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  .menu {
    height: auto;
    flex-direction: row;
    width: 100%;
    position: relative;
  }

  .sidebar_container h1 {
    text-align: center;
    font-size: 20px;
    width: 50%;
  }

  .nav {
    display: flex;
    flex-direction: row;
    /* justify-content: flex-end; */
    justify-content: inherit;
  }

  .link {
    margin: 0 10% 0 0;
    font-size: 16px;
    display: inline-block;
    min-width: 60px;
  }
  .link:last-child {
    margin: 0;
  }
}

@media (max-width: 500px) {
  .sidebar_container h1 {
    font-size: 16px;
    width: 40%;
  }

  .nav {
    width: 60%;
    justify-content: center;
  }

  .link {
    margin: 0 5% 0 0;
    font-size: 13px;
    display: inline-block;
    min-width: auto;
  }

  .activeLink,
  .link:hover {
    letter-spacing: 0;
    color: #fff;
  }
}
